//Resets
* {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

//Transitions
@mixin fade($opacity) {
  width: 100%;
  overflow-y: auto;
  position: fixed;
  opacity: $opacity;
}

.fade-enter {
  @include fade(0);

  &.fade-enter-active {
    @include fade(1);
    transition: opacity 300ms ease-in 300ms;
  }
}

.fade-exit {
  @include fade(1);

  &.fade-exit-active {
    @include fade(0);
    transition: opacity 300ms ease-in;
  }
}


.app-container {
  padding-bottom: 1rem;
}